import {Dispatch} from "redux";
import {usersAPI} from "../api/api";

export type ActionsType =
    ReturnType<typeof AddPostAC> |
    ReturnType<typeof setProfileDataAC> |
    ReturnType<typeof getStatusAC>


export type initialStateType = typeof initialState

type ResponseDataType = {
    userId: number
    lookingForAJob: boolean
    fullName: string
    contacts: {
        github: string
        instagram: string
    }
    photos: {
        small: string
        large: string
    }
}

const initialState = {
    posts: [
        {
            id: 1,
            message: 'Hello, how are you ?',
            likes: '15',
            dislike: '0',
        }
    ],
    PostText: "",
    data: {} as ResponseDataType,
    status: 'hello'
}


export const profileReducer = (state: initialStateType = initialState, action: ActionsType): initialStateType => {
    switch (action.type) {
        case 'ADD-POST': {
            let NewPost = {
                id: 5, message: action.text,
                likes: '',
                dislike: ''
            }
            return {
                ...state, posts:
                    [...state.posts, NewPost],
                PostText: action.text
            }
        }
        case "SET-PROFILE-DATA":
            return {
                ...state,
                data: action.data
            }
        case 'SET-STATUS':
            return {
                ...state,
                status: action.status
            }
        default:
            return state;
    }
}


export const AddPostAC = (text: string) => {
    // debugger
    return {
        type: "ADD-POST",
        text
    } as const
}

export const setProfileDataAC = (data: ResponseDataType) => {
    return {
        type: 'SET-PROFILE-DATA',
        data
    } as const
}

export const getStatusAC = (status: any) => {
    return {
        type: 'SET-STATUS',
        status
    } as const
}

export const getPrifileDataThunk = (id: string | undefined) => {
    return async (dispatch: Dispatch) => {
        try {
            let res = await usersAPI.getPrifileData(id)
            dispatch(setProfileDataAC(res.data))
        } catch (e) {
            console.log(e)
        }
    }
}

export const getStatus = (id: string | undefined) => {
    return async (dispatch: Dispatch) => {
        let res = await usersAPI.getStatus(id)
        dispatch(getStatusAC(res.data))
    }
}

export const updateStatus = (status: string) => async (dispatch: Dispatch) => {
    let response = await usersAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(getStatusAC(status))
    }
}