type initialStateType = typeof initialState

const initialState = {
    dialogs: [
        {id: 1, name: 'Andrey'},
        {id: 2, name: 'Evgeni'},
        {id: 3, name: 'Ruslan'},
    ],
    messages: [
        {id: 1, message: "Hello"},
        {id: 2, message: "How are you?"},
        {id: 3, message: "I am super man!"},
    ],
    messageText: "",
}

type ActionsType = updateNewMessageCreator

export const dialogsReducer = (state: initialStateType = initialState, action: ActionsType):initialStateType => {
    switch (action.type) {
        case 'NEW-DIALOGS-MESSAGE':
            return  {
                ...state,
                messages: [...state.messages, {id: 1, message: action.text}]
            };
        default:
            return state;
    }
}

type updateNewMessageCreator = ReturnType<typeof newDialogsMessageAC>


export const newDialogsMessageAC = (text: string) => {
    return {
        type: 'NEW-DIALOGS-MESSAGE',
        text
    } as const
}