import React from 'react';
import axios from "axios";


const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "89979e60-a28a-4a3b-8ea5-be423d731791"
    }
})

export const usersAPI = {
    GetUsers(currentPages: number, pageSize: number) {
        return instance(`users?page=${currentPages}&count=${pageSize}`)
            .then(response => {
                return response.data
            });
    },
    follow(userId: number) {
        return instance.post(`follow/${userId}`)
    },
    unfollow(userId: number) {
        return instance.delete(`follow/${userId}`)
    },
    userLogin() {
        return instance.get<ResponseDataType>(`auth/me`)
    },
    getPrifileData(id: string | undefined) {
        return instance.get(`profile/${id}`)
    },
    getStatus(id: string | undefined) {
        return instance.get(`profile/status/${id}`)
    },
    updateStatus(status: string) {
        return instance.put(`profile/status`, {status: status})
    }
}

export const authAPI = {
    me(){
        return instance.get('auth/me')
    },
    login(data: LoginDataType) {
        return instance.post<Request, ResponseDataType<{data: number}>>('auth/login', data)
    },
    logout(){
        return instance.delete('auth/login')
    }
}

export type LoginDataType = {
    email: string,
    password: string,
    rememberMe?: boolean,
    captcha?: boolean
}


export type ResponseDataType<T = {}> = {
    resultCode: number
    messages: []
    data: T
}

