import React from 'react';
import h from './Header.module.css'
import {NavLink} from "react-router-dom";
import {useAppDispatch, useAppSelector} from "../../redux/hooks";
import {logoutTC} from "../../redux/authReducer";


export const Header = () => {

    const dispatch = useAppDispatch()
    const isAuth = useAppSelector(state => state.auth.isAuth)
    const data = useAppSelector(state => state.profileReducer.data)


    const logOutButtonHandler = () => {
        dispatch(logoutTC())
    }

    return (
        <header className={h.header}>
            <img src={'https://nemchinsky.me/wp-content/uploads/2021/03/small-logo.svg'}/>
            <div className={h.login_block}>
                {isAuth ? <div style={{color: 'black'}}>{data.fullName} <button onClick={logOutButtonHandler}>log out</button></div>:
                    <NavLink className=
                             {({isActive}) =>
                             isActive ? h.activeLogin :
                             h.login_block}
                             to={'/login'}>
                        login
                    </NavLink>
                }
            </div>
            <nav className={h.nav}>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element}
                             to={'/profile/2'}>Profile</NavLink>
                </div>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element}
                             to={'/users'}>Users</NavLink>
                </div>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element}
                             to={'/messages'}>Messages</NavLink>
                </div>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element} to={'/news'}>News</NavLink>
                </div>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element}
                             to={'/music'}>Music</NavLink>
                </div>
                <div className={h.element}>
                    <NavLink className={({isActive}) => isActive ? h.activeLink : h.element}
                             to={'/settings'}>Settings</NavLink>
                </div>
            </nav>
            {/*<img*/}
            {/*    src={'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTJFo0pp4lTSkeJ9bQUrSYv1JhzX06K09lQlA&usqp=CAU'}/>*/}
        </header>
    );
};
