import {authAPI, LoginDataType} from "../api/api";
import {Dispatch} from "redux";

type InitialStateType = typeof initialState
type ActionsType = setAuthUserData | isLoggedInData
type isLoggedInData = ReturnType<typeof isLoggedInDataAC>
type setAuthUserData = ReturnType<typeof setAuthUserDataAC>


const initialState = {
    isLoggedIn: false,
    data: {
        userID: 0,
        email: '',
        login: ''
    },
    isAuth: false
}

export const authReducer = (state: InitialStateType = initialState, action: ActionsType): InitialStateType => {
    switch (action.type) {
        case 'SET-USER-DATA':
            return {
                ...state,
                data: action.data,
                isAuth: true
            }
        case 'login/SET-IS-LOGGED-IN':
            return {
                ...state,
                isLoggedIn: action.value
            }
        default:
            return state
    }
}


export const setAuthUserDataAC = (data: { userID: number, email: string, login: string, isAuth: boolean }) => {
    return {
        type: 'SET-USER-DATA',
        data
    } as const
}

export const getAuthUserData = () => async (dispatch: Dispatch) => {
    let res = await authAPI.me()
    if (res.data.resultCode === 0) {
        dispatch(setAuthUserDataAC(res.data.data))
    }
}


export const isLoggedInDataAC = (value: boolean) => {
    return {
        type: 'login/SET-IS-LOGGED-IN',
        value
    } as const
}
export const loginTC = (data: LoginDataType) => async (dispatch: Dispatch) => {
    let res = await authAPI.login(data)
    if (res.resultCode === 0) {
        // @ts-ignore
        dispatch(getAuthUserData())
    }
}

export const logoutTC = () => async (dispatch: Dispatch) => {
    let res = await authAPI.logout()
    if (res.data.resultCode === 0) {
        // @ts-ignore
        dispatch(getAuthUserData())
    }
}

