import {Dispatch} from "redux";
import {ResponseDataType, usersAPI} from "../api/api";

export type followAC = ReturnType<typeof setHeaderDataAC>
export type setHeaderisAuthAC = ReturnType<typeof setHeaderisAuthAC>

const initialState = {
    data: {} as ResponseDataType,
    isAuth: false
}

type initialStateType = typeof initialState
export type othersType = followAC | setHeaderisAuthAC

export const headerReducer = (state: initialStateType = initialState, action: othersType): initialStateType => {
    switch (action.type) {
        case 'SET_USER_DATA':
            return {
                ...state,
                data: action.data
            }
        case 'SET_USER_IS_AUTH':
            return {
                ...state,
                isAuth: action.isAuth
            }
        default:
            return state
    }
}

export const setHeaderDataAC = (data: ResponseDataType) => {
    return {
        type: 'SET_USER_DATA',
        data: data,
    } as const
}

export const setHeaderisAuthAC = (isAuth: boolean) => {
    return {
        type: 'SET_USER_IS_AUTH',
        isAuth: isAuth
    } as const
}

export const loginUserThunk = () => {
    return async (dispatch: Dispatch) => {
        let response = await usersAPI.userLogin()
        if (response.data.resultCode === 0) {
            dispatch(setHeaderDataAC(response.data))
            dispatch(setHeaderisAuthAC(true))
        }
    }
}