import React, {Component} from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import {AppStateType} from "../../redux/redux-store";
import {
    actionCurrentPageAC,
    FollowAC,
    followingCreator,
    getUserThunkCreator,
    onPageChangedThunkCreator,
    setTotalUsersCountAC,
    setUsersAC,
    toggleFetchingAC,
    togglefollowingProgressAC,
    unFollowAC,
    unfollowingCreator
} from "../../redux/users-reducer";
import Users from "./Users";
import {Preloader} from "../common/preloader/Preloader";
import {
    getCurrentPages,
    getFollowingProgress,
    getIsFetching,
    getPageSize,
    getTotalUserCount,
    getUsers
} from "../../redux/users-selectors";

type MapStatePropsType = {
    pageSize: number
    currentPages: number
    isFetching: boolean
}

type MapDispatchPropsType = {
    getUserThunkCreator: (pageNumber: number, pageSize: number) => void,
    onPageChangedThunkCreator: (pageNumber: number) => void
}

export type UsersPropsType = MapStatePropsType & MapDispatchPropsType

export const mapStateToProps = (state: AppStateType) => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUserCount: getTotalUserCount(state),
        currentPages: getCurrentPages(state),
        isFetching: getIsFetching(state),
        followingProgress: getFollowingProgress(state)
    }
}

export class UsersAPI extends Component<UsersPropsType> {

    componentDidMount() {
        this.props.getUserThunkCreator(this.props.currentPages, this.props.pageSize)
    }

    onPageChanged = (pageNumber: number) => {
        this.props.onPageChangedThunkCreator(pageNumber)
    }

    render() {
        return <>
            {this.props.isFetching && <Preloader/>}
            <Users onPageChanged={this.onPageChanged}/>
        </>
    }
}


export default compose<React.FC>(
    connect(mapStateToProps, {
        FollowAC,
        unFollowAC,
        setUsersAC,
        actionCurrentPageAC,
        setTotalUsersCountAC,
        toggleFetchingAC,
        togglefollowingProgressAC,
        getUserThunkCreator,
        onPageChangedThunkCreator,
        unfollowingCreator,
        followingCreator
    })
)(UsersAPI)

