import {usersAPI} from "../api/api";
import {Dispatch} from "redux";

export type followAC = ReturnType<typeof FollowAC>
export type unFollowACType = ReturnType<typeof unFollowAC>
export type setUsersAC = ReturnType<typeof setUsersAC>
export type actionCurrentPageAC = ReturnType<typeof actionCurrentPageAC>
export type setTotalUsersCountAC = ReturnType<typeof setTotalUsersCountAC>
export type toggleFetchingAC = ReturnType<typeof toggleFetchingAC>
export type togglefollowingProgressAC = ReturnType<typeof togglefollowingProgressAC>

export type InitialStateType = typeof initialState

export type UsersType = {
    name: string,
    id: number,
    uniqueUrlName: any,
    photos: {
        small: string
    },
    status: any,
    followed: boolean
}

const initialState = {
    users: [] as UsersType[],
    pageSize: 5,
    totalItemsCount: 0,
    currentPages: 1,
    isFetching: false,
    followingProgress: [] as any[]
}

export type othersType =
    followAC |
    unFollowACType |
    setUsersAC |
    actionCurrentPageAC |
    setTotalUsersCountAC |
    toggleFetchingAC |
    togglefollowingProgressAC

export const usersReducer = (state: InitialStateType = initialState, action: othersType): InitialStateType => {
    // debugger
    switch (action.type) {
        case 'FOLLOW-TYPE':
            return {
                ...state,
                users: state.users.map(u => u.id === action.userid ? {...u, followed: true} : u)
            }
        case 'UNFOLLOW-TYPE':
            return {
                ...state,
                users: state.users.map(u => u.id === action.userid ?
                    {...u, followed: false} : u)
            }
        case 'SET-USERS':
            return {
                ...state,
                users: action.users
            }
        case 'ACTION_CURRENT_PAGE':
            return {
                ...state,
                currentPages: action.currentPages
            }
        case 'SET_TOTAL_COUNT':
            return {
                ...state,
                totalItemsCount: action.totalCount
            }
        case 'TOGGLE_FETCHING':
            return {
                ...state,
                isFetching: action.isFetching
            }
        case 'TOGGLE_FOLLOWING_PROGRESS':
            return {
                ...state,
                followingProgress:
                    action.followingProgress ?
                        [...state.followingProgress, action.userID] :
                        state.followingProgress.filter(id => id !== action.userID)
            }
        default:
            return state
    }
}

export const FollowAC = (userid: number) => {
    return {
        type: 'FOLLOW-TYPE',
        userid
    } as const
}

export const unFollowAC = (userid: number) => {
    return {
        type: 'UNFOLLOW-TYPE',
        userid
    } as const
}

export const setUsersAC = (users: UsersType[]) => {
    return {
        type: 'SET-USERS',
        users
    } as const
}

export const actionCurrentPageAC = (currentPages: number) => {
    return {
        type: 'ACTION_CURRENT_PAGE',
        currentPages
    } as const
}

export const setTotalUsersCountAC = (totalCount: number) => {
    return {
        type: 'SET_TOTAL_COUNT',
        totalCount
    } as const
}

export const toggleFetchingAC = (isFetching: boolean) => {
    return {
        type: "TOGGLE_FETCHING",
        isFetching
    } as const
}

export const togglefollowingProgressAC = (followingProgress: boolean, userID: number) => {
    return {
        type: "TOGGLE_FOLLOWING_PROGRESS",
        followingProgress,
        userID
    } as const
}

export const followingCreator = (userId: number) => {
    return async (dispatch: Dispatch) => {
        dispatch(togglefollowingProgressAC(true, userId))
        const response = await usersAPI.follow(userId)
        if (response.data.resultCode === 0) {
            dispatch(FollowAC(userId))
        }
        dispatch(togglefollowingProgressAC(false, userId))
    }
}

export const unfollowingCreator = (userId: number) => {
    return async (dispatch: Dispatch) => {
        dispatch(togglefollowingProgressAC(true, userId))
        const response = await usersAPI.unfollow(userId)
        if (response.data.resultCode === 0) {
            dispatch(unFollowAC(userId))
        }
        dispatch(togglefollowingProgressAC(false, userId))
    }
}

export const getUserThunkCreator = (currentPages: number, pageSize: number) => {
    return async (dispatch: Dispatch) => {
        const data = await usersAPI.GetUsers(currentPages, pageSize)
        dispatch(toggleFetchingAC(false))
        dispatch(setUsersAC(data.items))
        dispatch(setTotalUsersCountAC(data.totalCount))
    }
}

export const onPageChangedThunkCreator = (pageNumber: number, pageSize: number) => {
    return async (dispatch: Dispatch) => {
        dispatch(toggleFetchingAC(true))
        dispatch(actionCurrentPageAC(pageNumber))
        const data = await usersAPI.GetUsers(pageNumber, pageSize)
        dispatch(setUsersAC(data.items))
        dispatch(toggleFetchingAC(false))
    }
}