import React, {useState} from 'react';
import {useAppSelector} from "../../../redux/hooks";
import styles from './Paginator.module.css'


type PaginatorType = {
    onPageChanged: (pageNumber: number) => void
}

export const Paginator = (props: PaginatorType) => {
    const portionSize = 10
    const usersStateData = useAppSelector(state => state.usersReducer)
    let pagesNumber = Math.ceil(usersStateData.totalItemsCount / usersStateData.pageSize)

    const portionCount = Math.ceil(pagesNumber / portionSize)

    const [portionNumber, setPortionNumber] = useState(1)
    const LeftPortionNumber = (portionNumber - 1) * portionSize + 1
    const RightPortionNumber = portionNumber * portionSize

    let pages = [];
    for (let i = 1; i <= pagesNumber; i++) {
        pages.push(i)
    }

    const onClickLeftButton = ()=>{
        setPortionNumber(portionNumber -1)
    }
    const onClickNextButton = ()=>{
        setPortionNumber(portionNumber + 1)
    }

    return (
        <div className={styles.wrapper}>
            {
               portionNumber > 1 && <button onClick={onClickLeftButton}>LEFT☺</button>
            }
                {
                    pages.filter(p => p >= LeftPortionNumber && p <= RightPortionNumber)
                        .map((elem) => {
                            return <span
                                onClick={(e) => {
                                    props.onPageChanged(elem)
                                }}
                                className={usersStateData.currentPages === elem ? styles.active : ''}
                                key={elem}>{elem}
                            </span>
                        })
                }
            {
                portionCount > portionNumber && <button onClick={onClickNextButton}>NEXT♥</button>
            }
        </div>
    );
};

