import React from 'react';
import styles from "../common/paginator/Paginator.module.css";
import cat from "../../photos/img.jpg";
import {NavLink} from "react-router-dom";
import {useAppSelector} from "../../redux/hooks";
import {useDispatch} from "react-redux";
import {followingCreator, unfollowingCreator, UsersType} from "../../redux/users-reducer";


type UserPropsType = {
    user: UsersType
}

export const User = (props: UserPropsType) => {

    const dispatch = useDispatch()
    const usersStateData = useAppSelector(state => state.usersReducer)


    return (
        <div className={styles.wrapper}>
                        <span>
                            <div>
                                <NavLink to={`/profile/${props.user.id}`}>
                                <img src={props.user.photos.small !== null ? props.user.photos.small : cat}
                                     className={styles.photo}/>
                                </NavLink>
                                </div>
                            <div>
                                {
                                    props.user.followed ?
                                        <button
                                            disabled={usersStateData.followingProgress.some(id => id === props.user.id)}
                                            onClick={() => {
                                                // @ts-ignore
                                                dispatch(unfollowingCreator(props.user.id))
                                            }}>follow</button> :
                                        <button
                                            disabled={usersStateData.followingProgress.some(id => id === props.user.id)}
                                            onClick={() => {
                                                // @ts-ignore
                                                dispatch(followingCreator(props.user.id))
                                            }}>unfollow</button>
                                }
                            </div>
                        </span>
                     <span>
                        <span>
                            <div><strong>name: </strong>{props.user.name}</div>
                            <div><strong>userID: </strong>{props.user.id}</div>
                        </span>
                        <span>
                            <div>{props.user.uniqueUrlName}</div>
                            {
                                props.user.status && <div><strong>status: </strong>{props.user.status}</div>
                            }
                            <div>{props.user.followed}</div>
                        </span>
                        </span>
        </div>
    )
};
